class Youtube {
    constructor(key) {
        this.key = key;
        this.getRequestOptions = {
            method: 'GET',
            redirect: 'follow'
        };    
    }

    async search(query) {
        const response = await fetch(`https://youtube.googleapis.com/youtube/v3/search?part=snippet&maxResults=25&q=${query}&type=video&key=AIzaSyC9LrWrMYN4z95E7FPKSqtfifM5d6u9oY0&key=${this.key}`, this.getRequestOptions);
        const result_1 = await response.json();
        return result_1.items.map(item => ({ ...item, id: item.id.videoId }));
    }

    async mostPopular() {
        const response = await fetch(`https://youtube.googleapis.com/youtube/v3/videos?key=AIzaSyC9LrWrMYN4z95E7FPKSqtfifM5d6u9oY0&part=snippet&chart=mostPopular&maxResults=25&key=${this.key}`, this.getRequestOptions);
        const result_1 = await response.json();
        return result_1.items;
    }
    
}

export default Youtube;